table {
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  width: 1150px;
  border-collapse: collapse;
  border: 3px solid rgb(68, 92, 75);
  margin-left:auto;
  margin-right:auto;
  margin-bottom:50px;
  padding-top:50px;
  padding-bottom:50px;
}
table th {
  text-transform: uppercase;
  text-align: left;
  background: rgb(159, 204, 132);
  color: #FFF;
  padding: 8px;
  min-width: 30px;
}
table td {
  text-align: left;
  padding: 8px;
  border-right: 2px solid #7D82A8;
}
table th:hover {
        background: #51e275;
}
table td:last-child {
  border-right: none;
}
table tbody tr:nth-child(2n) td {
  background: rgb(159, 204, 132);
}


